<template>
  <div>
    <form @submit="addTodo">
      <input
        type="text"
        v-model="title"
        name="title"
        placeholder="Enter your todo here"
      />
      <input type="submit" value="Submit" class="btn" />
    </form>
  </div>
</template>

<script>
import uuid from 'uuid';

export default {
  name: "AddTodo",
  data() {
    return {
      title: ""
    };
  },
  methods: {
    addTodo(e) {
      e.preventDefault();
      const newTodo = {
        id:uuid.v4(),
        title: this.title,
        completed: false
      };

      this.$emit("add-todo", newTodo);

      this.title = '';
    }
  }
};
</script>

<style scoped>
form {
  display: flex;
}

input[type="text"] {
  flex: 10;
  padding: 10px;
}

input[type="Submit"] {
  flex: 2;
}
.btn {
  background: #333;
  color: #fff;
  cursor: pointer;
}
</style>
